button {
    border-radius: 0;
    outline: none;
    appearance: none;
    -webkit-appearance: none;
    -moz-appearance:none;
    -webkit-border-radius: 0;
    -moz-border-radius: 0;
}

.main {
    display: flex;
    flex-direction: column;
    flex: 1 1 50%;
    @media (max-width: 900px) {
        flex: 1 1 100%;
    }

    h1 {
        margin: 10px 0;
        text-align: center;
        font-size: 20px;
        color: darkslategrey;
    }

    &__inputs {
        display: flex;
        gap: 10px;
        justify-content: space-between;
        margin: 0 0 10px 0;

        input {
            padding: 10px;
            
            border-radius: 5px;
            color: darkslategrey;
            font-weight: 600;
        }
    }

    &__field {
        border: 2px solid lightblue;
        width: 100%;
    }

    &__field:focus {
        border: 2px solid darkcyan;
    }

    &__button {
        transition: 0.2s;
        cursor: pointer;
        border: 0px transparent;
        background: lightblue;
        outline: none;
        appearance: none;
        -webkit-appearance: none;
        -moz-appearance: none;
        
    }

    &__button:hover {
        background: darkcyan;
        color: white;
    }

    &__block {
        display: flex;
        align-items: flex-start;
        gap: 20px;
        height: 100%;

        img {
            // border: 1px solid black;
            width: 100%;
            max-width: 128px;
        } 
    }

    &__info {
        display: flex;
        flex-direction: column;
        gap: 10px;
        color: darkslategrey;
        font-weight: 500;
    }

    &__help {
        display: flex;
        flex-direction: column;
        gap: 10px;
        

        h2 {
            margin: 10px 0 5px 0;
            text-align: center;
            font-size: 20px;
            color: darkslategrey;
        }
    }

    &__help-item {
        display: block;
        cursor: pointer;
        background: lightblue;
        padding: 5px;
        color: darkslategray;
        border: 0px transparent;
        border-radius: 5px;
        font-weight: 600;
        word-wrap:break-word;

        span {
            word-wrap: break-word;
        }
    }

    &__help-item:hover {
        background: darkcyan;
        color: white;

    }
}