.header {
    display: flex;
    width: 100%;
    background: darkslategrey;
    padding: 10px 20px;
    border-radius: 0 0 20px 20px;
    box-shadow: 3px 3px 3px grey;
    margin: 0 0 5px 0;
    display: flex;
    justify-content: space-between;
    align-items: center;

    &__logo {
        display: flex;
        align-items: center;
        gap: 5px;

        span {
            font-size: 18px;
            font-weight: 500;
            color: white;
        }

        img {
            max-width: 32px;
            max-height: 32px;
        }
    }

    &__nav {
        ul {
            display: flex;
            gap: 20px;
        }

        ul li a{
            transition: 0.2s;
        }

        ul li a:hover {
            color: gold;
        }

        ul li a{
            
            text-decoration: none;
            font-size: 16px;
            font-weight: 500;
        }
    }

}

.default-link {
    color: white;
}

.active-link {
    color: gold;
    
}