.recipesBlock {
    padding: 10px;
    // margin-top: 20px;
    
    h1 {
        text-align: center;
        font-size: 28px;
        font-weight: 500;
        color: darkslategrey;
        margin: 10px 0;
    }

    &__inputs {
        display: flex;
        justify-content: space-between;
        gap: 10px;
        max-width: 1000px;
        margin: 0 auto;
        margin-bottom: 20px;

        input {
            color: darkslategrey;
            border-radius: 5px;
            padding: 10px;
            font-weight: 600;
            font-size: 18px;
        }

    }

    &__field {
        // font-size: 18px;
        border: 2px solid lightblue;
        width: 100%;
        
    }

    &__field:focus {
        border: 2px solid darkcyan;
    }

    &__button {
        appearance: none;
        cursor: pointer;
        transition: 0.2s;
        background: lightblue;
 
    }

    &__button:hover {
        background: darkcyan;
        color: white;
    }

