.recipe {
    // flex-direction: column;
    // align-items: center;
    
    // flex: 1 1 25%;
    padding: 10px;
    border: 1px solid lightgrey;
        border-radius: 5px;
        display: inline-block;
    // background: rgb(255, 243, 216);

    h1 {
      
        padding: 0 0 10px 0;
        margin: 0 0 10px 0;
        border-bottom: 1px solid lightgray;
        font-style: italic;
    }

    &__image {
        width: 100%;

        img {
    
            width: 100%;
            max-width: 300px;
            max-height: 300px;
            // border: 0px transparent;
            // border-radius: 5px 5px 0 0;
            
        }
    }

    

    // &__row {
    //     // display: inline-block;
    //     border: 1px solid lightgray;
    //     box-shadow: 1px 1px 1px lightgray;
    //     display: flex;
    //     border: 0px transparent;
    //     border-radius: 5px;
    //     // max-width: 200px;
    //     background: rgb(250, 250, 250);
    //     // color: white;
    //     @media (max-width: 900px) {
    //         max-width: none;
    //         width: 100%;
    //         display: flex;
    //         flex-direction: column;
    //         align-items: center;
    //     }

    //     h1 {
    //         // color: white;
    //         margin: 5px;
    //     }
    // }

    &__row {
        
        display: flex;
        
    }

    &__info {
        display: flex;
        flex-wrap: wrap;
        // justify-content: flex-start;
        // flex-direction: column;
        gap: 10px;
        padding-top: 10px;

        span {
            padding: 5px;
            border: 0px transparent;
            border-radius: 5px;
            font-weight: 500;
        }
    }

    &__col {
        display: flex;
        flex-direction: column;

        h1 {
            margin: 0 0 10px 0;
        }

        ul {
            padding: 0 0 0 10px;
            display: flex;
            flex-direction: column;
            gap: 10px;
            
            
        }

        ul li {
            padding: 0 0 0 15px;
            color: darkslategray;
            font-weight: 500;
            position: relative;
            // font-style: italic;
        }

        ul li::before {
            content: '';
            position: absolute;
            top: 5px;
            left: 0;
            width: 6px;
            height: 6px;
            background: darkslategrey;
            border: 0px transparent;
            border-radius: 3px;
        }
    }

    &__cuisineType {
        
        background: lightgreen;
    }

    &__dishType {
        background: lightblue;
    }

    &__calories {
        background: lightcoral;
    }

    &__nutrients {
        margin: 10px 0 0 0;
        display: flex;
        gap: 10px;
        flex-wrap: wrap;
        // flex-direction: column;
   
        span {
        
            border: 0px transparent;
            border-radius: 5px;
            padding: 5px;
            background: lightgoldenrodyellow;
            font-weight: 500;
        }
    }
}

.small-col {
    flex: 1 1 40%;
}

.big-col {
    flex: 1 1 60%;
}

